.App{
    min-height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #282c34;
}

.container{
    display: flex;
    height: 80vh;
    border: 1px solid #557b20;
    padding: 20px;
    flex-direction: column;
    align-items: center;
    font-size: 20px;
    color: white;

}
.todoForm{
    display: flex;
    justify-content: space-around;
    width: 450px;
}
input{
    width: 80%;
    padding: 10px;
    border-radius: 20px;
    border: none;
}
button{
    border:none;
    padding: 10px;
    border-radius:20px;
    margin: 0 5px;
}
.allTodos{
    list-style: none;
    margin: 0;
    padding: 0;
}
.singleTodo{
    border-radius: 50px;
    margin: 10px 0;
    background-color: rgb(27, 174, 20);
    padding:10px 20px;
    display: flex;
    width: 420px;
    align-items: center;
}
.todoText{
    flex: 1;
}